import loading from '../../components/loadding'
import payBall from '../../components/payBall'
import commentItem from '../../components/commentItem'
export default {
    components: {
        loading,
        payBall,
        commentItem
    },
    created() {
        this.$nextTick(() => {            
            this.getVenueDetail()
        })
    },
    onLoad (option) {
        this.venueId = option.id
        this.rulesStr = option.str
    },    
    onShareAppMessage: function( options ){
        return this.$commenShare()
    },
    data() {
        return {
            showLoading: false,
            venueData: null,
            rulesStr: '',
            venueId: '',
            phoneCall: '',
            timer: null,
            currentTab: 1, // 1详情,2评论
            commentData: []
        }
    },
    computed: {
        routeArr() {
            if (this.venueData) {

                let arr = this.venueData.route.split(/；|;/)
                return arr
            }
            return []            
        }
    },
    methods: {
        async getVenueDetail () {
            const res = await this.$api.getVenueDetail(this.venueId)
            if (res.code === '0') {
                this.venueData = res.data
                this.processAddress(this.venueData.address)
                this.phoneCall = res.data.phone
            }
        },
        processAddress (val) {
            let str = val
            if (val.indexOf('<selfOptions>') >= 0) {
                let temp1 = str.split('<selfOptions>')[0]
                let temp2 = str.split('<selfOptions>')[1]
                this.venueData.address = temp1
                this.$set(this.venueData, 'coordinate',JSON.parse(temp2))
            } else {
                this.$set(this.venueData, 'coordinate',[{
                    lng: this.venueData.addressLng,
                    lat: this.venueData.addressLat,
                    name: this.venueData.name
                }])
            }
        },
        processImg (obj) {
            if (!obj) return
            let val = obj.description
            return val.replace(/\<img/g, '<img style="max-width:100%;height:auto" ')
        },
        callPhone () {
            let phoneCall = [this.phoneCall]
            uni.showActionSheet({
                itemList: [phoneCall[0]],
                success: function (res) {
                    wx.makePhoneCall({
                        phoneNumber: phoneCall[res.tapIndex], //此号码并非真实电话号码，仅用于测试  
                        success: function () {
                            console.log("拨打电话成功！")
                        },
                        fail: function () {
                            console.log("拨打电话失败！")
                        }
                    })
                },
                fail: function (res) {
                    console.log(res.errMsg)
                }
            })
        },
        gotoAppointment () {            
            uni.navigateTo({
                url: `/pages/appointmentPage/appointmentPage?id=${this.venueId}`
            })
        },
        gotoMap () {
            this.$tip.toast('请稍后...','none')
            let data = JSON.stringify(this.venueData.coordinate)
            console.log(data)
            uni.navigateTo({
                url: `../map/map?coordinates=${data}`
            })
        },
        changeCurrentTab (type) {
            this.currentTab = type
            if (type === 2) {
                this.getCommentList()
            }
        },
        inputComment () {
            uni.navigateTo({
                url: `../inputCommentPage/inputCommentPage?venueId=${this.venueId}`
            })
        },
        async getCommentList () {
            let params = {
                venueId: this.venueId,
                status: 1,
                pageSize: 100,
                pageNo: 1
            }
            const res = await this.$api.getCommentList(params)
            if (res.code === '0') {
                this.commentData = res.data.list
            }
        },
    },
}
